.home {
  display: flex;
  padding: 64px 16px;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

#mapid { height: 180px; }

.card-group {
  display: flex;
}

.livro {
  background-image: url("../../img/livro.jpg");
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.gigantes {
  background-image: url("../../img/giant2.jpg");
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.pirandello {
  background-image: url("../../img/pirandello2.jpg");
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.inspiracoes {
  background-image: url("../../img/giantsbr2.jpg");
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.elenco {
  background-image: url("../../img/elenco.png");
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.map {
  width: 500px;
  height: 700px;
}

.obra {
  background-image: url("../../img/livro.jpg");
}

.obraText {
  position: absolute;
}

.cartaz {
  display: flex;
  font-family: 'Saira Extra Condensed', sans-serif;
  align-items: center;
  margin: 48px 0px;
  flex-direction: column;
  background-color: none;
}

.cartaz > img {
  margin: 0px;
  width: 630px;
  box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
}

.map {
  width: 300px;
}

@media screen and (max-width: 768px) {
  .home {
    flex-direction: column;
    justify-content: center;
  }

  .card-group {
    display: flex;
    flex-direction: column;
  }

  .cartaz > img {
    width: 300px;
    box-shadow: 0 4px 8px 0 grey, 0 6px 20px 0 grey;
  }
}